@font-face {
  font-family: "OpenSans-Regular";
  src: url("../fonts/OpenSans-Regular.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-SemiBold";
  src: url("../fonts/OpenSans-SemiBold.woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans-Bold";
  src: url("../fonts/OpenSans-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "OpenSans-Regular", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #D2E9E3;
  background-color: #2C3532;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 15px;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: inherit;
}

.link-reset {
  text-decoration: none;
  color: inherit;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

section {
  padding: 50px 0;
}
@media (max-width: 768px) {
  section {
    padding: 25px 0;
  }
}

.title {
  margin: 0;
  margin-bottom: 40px;
  font-family: "OpenSans-Bold", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 41px;
  text-align: center;
}
@media (max-width: 768px) {
  .title {
    font-size: 26px;
  }
}
@media (max-width: 425px) {
  .title {
    margin-bottom: 10px;
    font-size: 24px;
  }
}

.text {
  margin: 0;
  font-size: 20px;
  line-height: 27px;
}
@media (max-width: 768px) {
  .text {
    font-size: 18px;
  }
}
@media (max-width: 425px) {
  .text {
    font-size: 16px;
  }
}

body.lock {
  overflow: hidden;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  z-index: 10;
  background-color: #116466;
  padding: 15px 0;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  position: relative;
  width: 200px;
  height: 45px;
  z-index: 5;
}
.logo__img {
  width: 100%;
  height: 100%;
}

.burger {
  display: none;
}

.menu__item {
  position: relative;
  color: #D2E9E3;
}
.menu__link {
  position: relative;
}
.menu__link::after {
  position: absolute;
  content: "";
  width: 0;
  border-top: 2px dashed white;
  top: 105%;
  right: 0;
  transition: all 0.4s;
}
.menu__link:hover::after {
  width: 100%;
  left: 0;
}

/*-------------------------*/
body.touch .menu__link {
  flex: 1 1 auto;
  transition: transform 0.4s ease 0s;
}

/*-------------------------*/
@media screen and (min-width: 980px) {
  .menu {
    display: flex;
    align-items: center;
  }
  .menu__item {
    margin-right: 30px;
    color: #D2E9E3;
  }
  .menu__item:last-child {
    margin-right: 0;
  }
}
@media screen and (max-width: 980px) {
  .burger {
    z-index: 5;
    display: block;
    width: 30px;
    height: 18px;
    position: relative;
    cursor: pointer;
  }
  .burger::before, .burger::after, .burger__line {
    position: absolute;
    left: 0;
    border-top: 2px solid #D2E9E3;
    width: 100%;
    transition: all 0.4s ease 0s;
  }
  .burger::before, .burger::after {
    content: "";
  }
  .burger::before {
    top: 0;
  }
  .burger::after {
    bottom: 0;
  }
  .burger__line {
    top: 50%;
    transform: scale(1) translate(0, -50%);
  }
  .burger.active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0, -50%);
  }
  .burger.active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0, 50%);
  }
  .burger.active .burger__line {
    transform: scale(0) translate(0, -50%);
  }
  .nav {
    display: flex;
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    padding: 100px 30px 30px 30px;
    transition: left 0.4s ease 0s;
    overflow: auto;
  }
  .nav::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 77px;
    background-color: #116466;
    z-index: 2;
  }
  .nav.active {
    left: 0;
  }
  .menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
  }
  .menu__item {
    flex-wrap: wrap;
    margin-bottom: 30px;
    font-size: 25px;
  }
  .menu__item:last-child {
    margin-bottom: 0;
  }
}
.hero {
  padding-top: 160px;
  padding-bottom: 50px;
}
@media (max-width: 768px) {
  .hero {
    padding-top: 135px;
    padding-bottom: 25px;
  }
}
.hero__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}
@media (max-width: 768px) {
  .hero__wrap {
    gap: 0;
  }
}
.hero__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 320px;
  width: 100%;
}
.hero__inner-img {
  margin-bottom: 25px;
  width: 250px;
  line-height: 0;
}
.hero__img {
  width: 100%;
  height: auto;
  border-radius: 50%;
}
.hero__title {
  margin-bottom: 10px;
  white-space: nowrap;
  font-size: 32px;
  line-height: 44px;
}
@media (max-width: 425px) {
  .hero__title {
    font-size: 27px;
  }
}
.hero__subtitle {
  margin-bottom: 0;
  font-family: "OpenSans-SemiBold", sans-serif;
  font-weight: 600;
  font-size: 25px;
  line-height: 34px;
}
@media (max-width: 768px) {
  .hero__subtitle {
    margin-bottom: 15px;
  }
}
.hero__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__heading {
  margin-bottom: 15px;
}
.hero__text {
  max-width: 710px;
  text-align: center;
}

.skills__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 510px) {
  .skills__wrap {
    align-items: center;
  }
}
.skills__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 50px 120px;
}
.skills__card {
  width: 200px;
}
.skills__card:hover .skills__content {
  transform: rotateY(-180deg);
}
.skills__card:hover .skills__icon {
  opacity: 0;
}
.skills__card:hover .skills__inner {
  opacity: 1;
}
.skills__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  transition: transform 0.4s;
}
.skills__icon {
  width: 100%;
  height: auto;
  transition: opacity 0.4s;
  z-index: -1;
}
@media (max-width: 768px) {
  .skills__icon {
    max-width: 120px;
  }
}
.skills__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  position: absolute;
  top: 0;
  padding-top: 20px;
  min-height: 100%;
  background-color: #116466;
  transition: all 0.4s;
  border-radius: 20px;
  opacity: 0;
  pointer-events: none;
  transform: rotateY(-180deg);
}
.skills__heading {
  margin-bottom: 10px;
  font-family: "OpenSans-SemiBold", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
}
.skills__text {
  text-align: center;
}

.works__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.works__inner {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  gap: 20px;
}
.works__link {
  max-width: 350px;
  line-height: 0;
  position: relative;
}
.works__link:hover .works__content {
  background-color: rgba(17, 100, 102, 0.9);
}
.works__link:hover .works__heading {
  top: 40%;
  opacity: 1;
  transform: translate(-50%, -50%);
}
.works__link:hover .works__excerpt {
  bottom: 15%;
  opacity: 1;
  transform: translate(-50%, -50%);
}
@media (max-width: 385px) {
  .works__link:hover .works__heading {
    top: 40%;
  }
  .works__link:hover .works__excerpt {
    bottom: 20%;
  }
}
.works__img {
  width: 100%;
  height: 230px;
  border-radius: 30px;
}
.works__content {
  padding: 0 10px;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 30px;
  top: 0;
  left: 0;
  transition: background-color 0.6s;
}
.works__heading {
  margin: 0;
  position: absolute;
  font-family: "OpenSans-SemiBold", sans-serif;
  font-weight: 600;
  font-size: 25px;
  line-height: 34px;
  text-transform: capitalize;
  top: 0;
  left: 50%;
  opacity: 0;
  transform: translate(-50%, 0);
  transition: all 0.4s;
}
@media (max-width: 385px) {
  .works__heading {
    font-size: 20px;
  }
}
.works__heading--all-caps {
  text-transform: uppercase;
}
.works__excerpt {
  position: absolute;
  width: 100%;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  bottom: 0;
  left: 50%;
  opacity: 0;
  transform: translate(-50%, 0);
  transition: all 0.4s;
}
.works__excerpt--already {
  width: 70%;
}
.works__excerpt--rusmaster {
  width: 45%;
}
@media (max-width: 385px) {
  .works__excerpt {
    font-size: 16px;
    line-height: 20px;
    width: 90%;
  }
  .works__excerpt--already {
    width: 60%;
  }
  .works__excerpt--rusmaster {
    width: 40%;
  }
}

.merit__wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.merit__title {
  margin: 0 auto;
  margin-bottom: 20px;
}
@media (max-width: 765px) {
  .merit__title {
    margin-bottom: 10px;
  }
}

.contacts__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contacts__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 50px;
  max-width: 465px;
  width: 100%;
}
.contacts__link {
  display: flex;
  align-items: center;
}
.contacts__icon {
  max-width: 80px;
  height: 80px;
  transition: transform 0.4s;
}
.contacts__icon:hover {
  transform: scale(0.95);
}
@media (max-width: 415px) {
  .contacts__icon {
    max-width: 60px;
    height: 60px;
  }
}

.footer {
  padding: 25px 0;
}
.footer__container {
  display: flex;
  justify-content: center;
}
.footer__text {
  text-align: center;
}

.spollers {
  width: 100%;
}
.spollers__item {
  position: relative;
}
.spollers__item::after {
  position: absolute;
  content: "";
  width: 100%;
  border-top: 1px solid #D2E9E3;
  top: 100%;
  left: 0;
}
.spollers__item:last-child::after {
  display: none;
}
.spollers__title {
  font-family: "OpenSans-SemiBold", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 27px;
  position: relative;
  padding: 10px;
  width: 100%;
  text-align: left;
  color: #D2E9E3;
}
@media (max-width: 425px) {
  .spollers__title {
    font-size: 18px;
  }
}
.spollers__text {
  padding: 0 10px 10px 15px;
  color: #D2E9E3;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 425px) {
  .spollers__text {
    font-size: 16px;
  }
}
.spollers.init .spollers__title {
  padding: 20px 50px 20px 0;
}
@media (max-width: 768px) {
  .spollers.init .spollers__title {
    padding: 10px 50px 10px 0;
  }
}
.spollers.init .spollers__title::before, .spollers.init .spollers__title::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 50%;
  width: 20px;
  border-top: 2px solid #D2E9E3;
  transition: transform 0.3s ease 0s;
}
.spollers.init .spollers__title::after {
  transform: rotate(-45deg);
}
.spollers.init .spollers__title::before {
  right: 24px;
  transform: rotate(45deg);
}
.spollers.init .spollers__title.active::after {
  transform: rotate(45deg);
}
.spollers.init .spollers__title.active::before {
  transform: rotate(-45deg);
}